/**
 * input 
 *
 * @prop block - 
 * @prop hidden - 
 * @prop initVal - 
 * @prop number -  nmuber 
 * @prop placeholder - 
 * @prop param - 
 * @prop readOnly - 
 * @prop required - 
 * @prop row - textarea 
 * @prop textLengthTip - 
 * @prop type - ( field | area )
 *
 * @prop completion - 
 * @prop errorMessage - input 
 * @prop errorTipType -  bubble | tip 
 * @prop formatMessage - 
 * @prop min - inputtextarea 
 * @prop max - inputtextarea 
 * @prop minNum - inputtextarea 
 * @prop maxNum - inputtextarea 
 * @prop regex - 
 * @prop verifiedType - 
 *
 * @prop headerSpan - 
 * @prop footerSpan - 
 *
 * @event change - input
 * @event blur - inputblur
 * @event focus - inputfocus
 * @event keyup - inputkeyup
 *
 * @slot header
 * @slot footer
 */

import './Input.scss'
import './Input.material.scss'
import './Input.bootstrap.scss'
import render from './Input.render'

import store from '../../vuex/store'
import hubStore from '../../vuex/module/hub/type.json'
import initVerfication from './validate.js'

import baseMixin from '../../mixin/base'
import formMixin from '../../mixin/form'
import apiMixin from './Input.api'

import rowComp from '../Row/Row'
import colComp from '../Col/Col'

import {
  dataType
} from '../../util/data/data'
import tip from '../Message/tip'

const TYPE_TEXT_AREA = 'area'
const TYPE_TEXT = 'field'
const ERROR_MESSAGE_TIP = 'tip'
const ERROR_MESSAGE_BUBBLE = 'bubble'

export default {
  name: 'Input',

  render,

  mixins: [baseMixin, formMixin, apiMixin],

  components: {
    row: rowComp,
    column: colComp
  },

  store,

  props: {
    block: {
      type: Boolean,
      default: false
    },
    hidden: {
      type: Boolean,
      default: false
    },
    initVal: {
      type: [String, Number],
      default: ''
    },
    number: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: ''
    },
    param: {
      type: String,
      default: ''
    },
    readOnly: {
      type: Boolean,
      default: false
    },
    row: {
      type: Number,
      default: 4
    },
    textLengthTip: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: 'field'
    },
    required: {
      type: Boolean,
      default: true
    },
    errorMessage: {
      type: String,
      default: ''
    },
    errorTipType: {
      type: String,
      default: 'tip'
    },
    formatMessage: String,
    min: Number,
    max: Number,
    minNum: Number,
    maxNum: Number,
    regex: String,
    verifiedType: String,
    completion: {
      type: Boolean,
      default: false
    },
    headerSpan: {
      type: Number,
      default: 1
    },
    footerSpan: {
      type: Number,
      default: 1
    }
  },

  data() {
    this.compName = 'input' // 

    return {
      value: this.number ? this._switchNum(this.initVal) : this.initVal, // 
      focusing: false, //  focus 
      keyuping: false, //  keyup 
      dangerTip: '', // 
      dataTypeName: '', // 
      verified: true, // 
      bubbleDisplay: false, // 
      inputTextLength: 0, // 
      errorBorderDisplay: false // 
    }
  },

  computed: {
    cPrefix() { // 
      return `${this.compPrefix}-input`
    },
    _formatMessage() { // 
      return this.errorMessage ? this.errorMessage + '' : this.dataTypeName + ''
    },
    dangerTipDisplay() {
      return !!this.dangerTip && this.bubbleDisplay
    },
    isTextarea() {
      return this.type === TYPE_TEXT_AREA
    },
    isText() {
      return this.type === TYPE_TEXT
    },
    inputHub() {
      return this.$store.getters[hubStore.input.get]
    },
    stageClass() { //  stage  class 
      return [{
        [`${this.cPrefix}-textarea-stage`]: this.isTextarea
      }]
    },
    wrapClass() {
      return [
        this.xclass('wrap'),
        {
          [`${this.cPrefix}-editting`]: this.focusing
        },
        {
          [`${this.cPrefix}-error-border`]: this.errorBorderDisplay
        }
      ]
    },
    inputBoxCol() { // input 
      let slotHead = this.$slots.header
      let slotTail = this.$slots.footer

      if (slotHead && slotTail) {
        return 12 - this.footerSpan - this.headerSpan
      } else if (slotHead) {
        return 12 - this.headerSpan
      } else if (slotTail) {
        return 12 - this.footerSpan
      } else {
        return 12
      }
    }
  },

  watch: {
    initVal(val, oldVal) {
      this.value = val
    },
    value(val, oldVal) {
      // 
      this.limitLen = String(val).length

      if (this.completion && this.$slots.completion) {
        this.$slots.completion[0].componentInstance.search(val)
      }

      this._dispatchChange()
    }
  },

  methods: {
    /**
     * 
     * @return {Object} this - 
     */
    _initVerfication() {
      if (this.regex) {
        this.regexObj = new RegExp(this.regex)

        return this
      }

      var verify = initVerfication(this.verifiedType)

      if (verify) {
        this.regexObj = verify.regex
        this.dataTypeName = verify.dataTypeName
      }

      return this
    },

    /**
     * 
     * @return {Object} this - 
     */
    _initVerfiedMessage() {
      let errorMessage = this.errorMessage

      if (errorMessage) {
        this.emptyMessage = errorMessage
        this.lengthMessage = errorMessage

        return this
      }

      this.emptyMessage = this.errorMessage ? this.errorMessage : ''
      this.lengthMessage = this.errorMessage ? this.errorMessage : ''

      return this
    },

    /**
     *  value  change 
     * @return {Object} this - 
     */
    _dispatchChange() {
      return this.$emit('change', {
        emitter: this,
        value: this.value
      })
    },

    /**
     * 
     *
     * @return {Object} -
     *                  verified - 
     *                  dangerTip - 
     */
    _verifyEmpty(firstVerify) {
      let dangerTip = ''

      if (this.required) {
        if (this.bubbleDisplay) {
          dangerTip = firstVerify ? '' : `${this.emptyMessage}!`
        } else {
          dangerTip = `${this.emptyMessage}!`
        }

        return {
          verified: false,
          dangerTip
        }
      }

      return {
        verified: true,
        dangerTip
      }
    },

    /**
     *  -  16 
     */
    _switchNum(val) {
      if (val === 0 || val === '0') {
        return 0
      }

      let strTemp = String(val)

      if (isNaN(strTemp)) {
        let temp = strTemp

        strTemp = strTemp.replace(/[^\d.]+/g, '')

        if (temp.indexOf('-') === 0) {
          strTemp = '-' + strTemp
        }
      }

      if (isNaN(strTemp)) {
        strTemp = ''
      }

      if (val.length >= 16) {
        return strTemp
      }

      return Number(strTemp)
    }
  },

  created() {
    this.bubbleDisplay = this.errorTipType !== ERROR_MESSAGE_TIP
  },

  mounted() {
    this._initVerfication()
    this._initVerfiedMessage()

    this.$store.dispatch(hubStore.input.add, this)
  }
}



